*{
    margin: 0;
    padding:0;
    box-sizing: border-box;
}
html{
    scroll-behavior: smooth;
}
.background{
    background: black;
}
.container{
    font-family: 'Prompt', sans-serif;
    font-size: 18px;
    height: 100%;
    max-width: 1920px;
    margin:0 auto;
}
.menubar{
    z-index: 99;
    display: inline-block;
    background: #000000;
    height: 75px;
    width: 100%;
    position: -webkit-sticky;
    position: sticky;
    top:0;
    -webkit-box-shadow: 0px 6px 6px 2px rgba(0,0,0,0.34); 
    box-shadow: 0px 6px 6px 2px rgba(0,0,0,0.34);    
}
#icon{
    padding:0px;
    margin-left: 20px;
    transition: transform .2s;
}
#icon:hover{
    transform: scale(1.1);
}
.menubar a{
    margin:0;
    display: inline-block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px;
    text-decoration: none;
}
.link::after{
    content: '';
    display: block;
    width: 0;
    height: 2px;
    background: transparent;
    transition: all .3s;
}
.link:hover::after{
    width: 100%;
    background:#f2f2f2;
    transition: width .3s;
}
.time{
    float: right; 
    padding: 14px;
    vertical-align: middle;
    display: inline-block;
    text-align: center;
    font-size: 12px;
}

.footer{
    /* margin-top: 50px; */
    /* margin: 0 auto; */
    max-width: 1920px;
    height: 200px;
    position: relative;
    font-size: 16px;
    padding-top:15px;
    padding-left: 20px;
    background-color: #808080;
    text-align: center;
}
.home,.profile,.education{
    max-width: 1920px;
    height: 900px;
}
.home{
    background: url("https://cdn.discordapp.com/attachments/299080565242462208/811803932333965312/Y4BHWJM.png");
    background-repeat: no-repeat;
    background-size: 1920px 1080px;
    padding: 80px;
    display: block;
}
#head{
    font-size: 30px;
    color:#fefefe;
    text-align: center;
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%);
    width: 800px;
    height: 400px;
}
#black{
    color:#000000;
}
.inter1{
    background: linear-gradient(to bottom, #1a081c 0%, #000000 80%);
    max-width: 1920px;
    height: 150px;
}
.inter2{
    /* background: linear-gradient(to bottom, #fefefe 0%,#79a0c4 50%, #537caa 100%); */
    background-color: #fefefe;
    max-width: 1920px;
    height: 150px;
}
.profile{
    padding-top: 100px;
    padding-left: 50px;
    padding-right: 50px;
    background-color: #fefefe;
    color:#000000;
    /* border-top: 2px dashed black; */
}
.education{
    padding-top: 100px;
    background-image:url("https://cdn.discordapp.com/attachments/299080565242462208/811804482228584458/gIT2AXw-blured.png");
    height: 1000px;
}
#pic1{
    /* top:50%; */
    float:left;
    width: 450px;
    height: 450px;
    margin-left:20%;
    margin-top: 100px;
    display: block;
    transition: transform .2s;
}
#pic1:hover{
    transform: scale(1.2);
}
#text1{
    float: left;
    width: 500px;
    height: 400px;
    margin-top: 100px;
    margin-left:100px;
    display: block;
    font-size: 24px;
    padding-top: 120px;
}
@media screen and (max-width: 1500px){
    #pic1 img{
        width: 200px;
        height: 200px;
    }
    #pic1{
        left:50%;
        width: 200px;
        height: 200px;
    }
    #text1{
        margin-top:50px;
        margin-right:0;
        float:left;
        padding-top:0;
    }
    }
.timeline{
    position: relative;
    margin: 0 auto;
    max-width: 1200px;
}
.timeline::after{
    content: '';
    position: absolute;
    width: 6px;
    background-color: #fefefe;
    top: 0;
    bottom: 0;
    left: 50%;
    margin-left: -3px;
}
.box {
    padding: 10px 40px;
    position: relative;
    background-color: inherit;
    width: 50%;
  }
.box::after {
    content: '';
    position: absolute;
    width: 25px;
    height: 25px;
    right: -17px;
    background-color: #fefefe;
    border: 4px solid #008cff;
    top: 15px;
    border-radius: 50%;
    z-index: 1;
}
.left {
    left: 0;
}
.right {
    left: 50%;
}
.left::before {
    content: " ";
    height: 0;
    position: absolute;
    top: 22px;
    width: 0;
    z-index: 1;
    right: 30px;
    border: medium solid #fefefe;
    border-width: 10px 0 10px 10px;
    border-color: transparent transparent transparent #fefefe;
}
.right::before {
    content: " ";
    height: 0;
    position: absolute;
    top: 22px;
    width: 0;
    z-index: 1;
    left: 30px;
    border: medium solid #fefefe;
    border-width: 10px 10px 10px 0;
    border-color: transparent #fefefe transparent transparent;
}
.right::after {
    left: -16px;
}
.content {
    color: #000000;
    padding: 20px 30px;
    background-color: #fefefe;
    position: relative;
    border-radius: 6px;
}
@media screen and (max-width: 600px) {
.timeline::after {
    left: 31px;
}
.container {
    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
}
.container::before {
    left: 60px;
    border: medium solid white;
    border-width: 10px 10px 10px 0;
    border-color: transparent white transparent transparent;
}
.left::after, .right::after {
    left: 15px;
}
.right {
    left: 0%;
    }
}
.contact img{
    width: 100px;
    height: 100px;
    margin-top:10px;
    margin-left: 50px;
    margin-right: 50px;
}
.contact{
    color:#fefefe;
    max-width: 100%;
    height: 160px;
    overflow: hidden;
}
.but{
    float:left;
    width:25%;
}
.contact a{
    color:#fefefe;
    text-decoration: none; 
}
footer{
    color:#fefefe;
}